import React from 'react';
import Card from './Card';
import {useDispatch} from 'react-redux';
import {deleteItem, changeDone} from '../actions/listAction';
function DoneImg(props) {
  if(props.done){
    return <img src="./assets/done.png" alt="done" width="30px" /> 
  }else{
    return<img src="./assets/undone.png" alt="undone" width="30px" /> 
  }
}
export default function ListItem(props) {
  const dispatch = useDispatch(); 
  return(
    <li>
        <Card className={props.item.done? "done item":"item"}>
        {props.item.text}
          <div>
            <button onClick={()=>{dispatch(changeDone(props.item.id))}}><DoneImg done={props.item.done} /></button>
            <button onClick={()=>{dispatch(deleteItem(props.item.id))}}>
              <img src="./assets/bin.png" alt="remove" width="30px" /> 
            </button>
          </div>
        
        </Card> 
        
    </li>
  )
}